.wrapper {
  background: 
  /* top, transparent chartreuse, radial grandient so the background image is more visible at the center of the page */ 
  -webkit-radial-gradient(
    rgb(180, 208, 103, .85), 
    rgb(180, 208, 103, 1)
  ),
  /* bottom, image */
  url("http://www.carasaven.com/wp-content/uploads/2017/02/CaraSaven-BananaLeavesGraphic-Wallpaper.jpg");
  background: 
  /* top, transparent chartreuse, radial grandient so the background image is more visible at the center of the page */ 
  -o-radial-gradient(
    rgb(180, 208, 103, .85), 
    rgb(180, 208, 103, 1)
  ),
  /* bottom, image */
  url("http://www.carasaven.com/wp-content/uploads/2017/02/CaraSaven-BananaLeavesGraphic-Wallpaper.jpg");
  background: 
  /* top, transparent chartreuse, radial grandient so the background image is more visible at the center of the page */ 
  radial-gradient(
    rgb(180, 208, 103, .85), 
    rgb(180, 208, 103, 1)
  ),
  /* bottom, image */
  url("http://www.carasaven.com/wp-content/uploads/2017/02/CaraSaven-BananaLeavesGraphic-Wallpaper.jpg");
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  overflow: auto;
  padding-bottom: 10px;
}
/*css for very small screen widths*/
@media only screen and (max-width:554px) {
  .wrapper{
    padding-bottom: 60px;
  }
}
/*css for very small screen widths*/
@media only screen and (max-width:447px) {
  .wrapper{
    padding-bottom: 10px;
  }
}




